/* ============ FONTS END ============ */

/* ============ CSS RESET START ============ */
/* http://meyerweb.com/eric/tools/css/reset/ 
     v2.0 | 20110126
     License: none (public domain)
  */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* ============ CSS RESET END ============ */

/* ============ COMMON STYLES START ============ */
*,
::before,
::after {
  box-sizing: border-box;
}
::selection {
  background-color: #fbd784;
  color: #000;
}
html {
  scroll-behavior: smooth;
}
:root {
  --brand-color: #fbd784;
  --bg-color: #0b1d26;
}
a,
span,
::before,
::after {
  display: inline-flex;
  text-decoration: none;
  color: inherit;
}
body {
  font-family: "Gilroy", system-ui, -apple-system, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 18px;
  font-weight: 700;
  background-color: var(--bg-color);
  color: #fff;
}
a {
  color: #fff;
  padding: 10px;
  transition: 0.3s;
}
a:hover {
  color: var(--brand-color);
}
.wide-container {
  max-width: 1790px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
}
.container {
  max-width: 1490px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}
.small-title {
  display: inline-flex;
  align-items: center;
  color: var(--brand-color);
  letter-spacing: 6px;
  text-transform: uppercase;
}
.small-title::before {
  content: "";
  width: 72px;
  height: 2px;
  background-color: var(--brand-color);
  margin-right: 30px;
}
/* ============ COMMON STYLES END ============ */

/* ============ HEADER START ============ */
.header {
  padding-top: 50px;
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 1;
}
.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  font-family: "Chronicle Display", serif;
  font-size: 32px;
  font-weight: 700;
  letter-spacing: 0.01em;
  margin-left: -10px;
}
.menu {
  display: flex;
}
.menu li:nth-child(n + 2) {
  margin-left: 20px;
}
.account {
  margin-right: -10px;
  column-gap: 10px;
}
/* ============ HEADER END ============ */

/* ============ HERO START ============ */
.hero-section {
  background: linear-gradient(
      344.24deg,
      rgba(11, 29, 38, 0) 49.06%,
      var(--bg-color) 108.93%
    ),
    linear-gradient(180deg, rgba(11, 29, 38, 0) 58%, var(--bg-color) 86.38%),
    url("../img/full-bg.webp") no-repeat 50% / cover;
  padding-bottom: 1110px;
}
.hero-section a {
  padding: 10px;
  transition: 0.3s;
}
.hero-section a:hover {
  color: var(--brand-color);
}
.content {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 290px;
}
.social-block {
  display: flex;
  align-items: center;
  transform: rotateZ(90deg);
  margin-left: -77px;
}
.social-networks {
  display: flex;
  margin-left: 15px;
}
.social-networks a {
  padding: 10px;
  font-size: 24px;
  transform: rotateZ(-90deg);
}
.social-networks li:nth-child(n + 2) {
  margin-left: 10px;
}
.social-networks a:hover {
  color: var(--brand-color);
}
.offer {
  width: 963px;
  margin: 0 auto;
}
.offer .swiper-slide * {
  margin-top: 30px;
}
.offer .swiper-slide *:first-child {
  margin-top: 0;
}
.offer h3 {
  font-family: "Chronicle Display", serif;
  font-weight: 500;
  font-size: 88px;
  text-transform: capitalize;
}
.offer a {
  align-items: center;
}
.offer .arrow {
  margin-top: 0;
  margin-left: 17px;
  font-size: 30px;
}
.offer-slider.swiper-container {
  height: 310px;
  position: static;
}

.offer-slider .swiper-pagination {
  position: absolute;
  top: 10px;
  right: 10px;
  width: auto !important;
  left: auto !important;
  margin: 0;
}
.offer-slider.swiper-container-vertical > .swiper-pagination-bullets {
  top: 74%;
  right: 0;
}
.offer-slider .swiper-pagination-bullet {
  padding: 17px 30px 17px 10px;
  width: auto;
  height: auto;
  text-align: right;
  font-size: 18px;
  opacity: 1;
  background: transparent;
  transition: 0.3s;
}
.offer-slider .swiper-pagination-bullet-active {
  color: var(--brand-color);
}
.offer-slider.swiper-container-vertical > .swiper-scrollbar {
  top: calc(50% + 25px);
  right: 0;
  width: 3px;
  height: 240px;
}
.offer-slider > .swiper-scrollbar {
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0;
}
.offer-slider .swiper-scrollbar-drag {
  background-color: #fff;
}
/* ============ HERO END ============ */

/* ============ INFO BLOCK START ============ */
.info-section {
  padding-bottom: 100px;
  margin-top: -380px;
}
.info-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.info-block:nth-child(odd) {
  padding-left: 150px;
}
.info-block:nth-child(n + 2) {
  margin-top: 200px;
}
.info-block img {
  width: 566px;
  height: 720px;
  object-fit: cover;
}
.info-block:nth-child(odd) > img {
  order: 1;
}
.info-block-content {
  position: relative;
  width: 49%;
}
.info-block-content::before {
  content: attr(data-num);
  position: absolute;
  left: 0;
  top: 0;
  font-size: 240px;
  opacity: 0.1;
  transform: translate(calc(-50% - 28px), calc(-50% + 10px));
}
.info-block-content:nth-child(even) {
  width: 41%;
}
.info-block-content * {
  margin-top: 30px;
}
.info-block-content *:first-child {
  margin-top: 0;
}
.info-block-content h3 {
  font-family: "Chronicle Display", serif;
  font-size: 64px;
  font-weight: 500;
}
.info-block-content p {
  font-weight: 700;
  line-height: 1.77;
}
.info-block-content a {
  color: var(--brand-color);
  align-items: center;
  margin-left: -10px;
}
.info-block-content a:hover {
  color: #fff;
}
.info-block-content span {
  font-size: 29px;
  margin-left: 16px;
}
/* ============ INFO BLOCK END ============ */

/* ============ FOOTER START ============ */
.footer {
  padding-top: 100px;
  padding-bottom: 120px;
  line-height: 1.77;
}
.footer a {
  padding: 10px;
  transition: 0.3s;
  margin-left: -10px;
}
.footer a:hover {
  color: var(--brand-color);
}
.footer-offer {
  width: 300px;
}
.footer .logo {
  margin-top: -10px;
  margin-bottom: 25px;
}
.footer h2 {
  color: var(--brand-color);
  font-size: 24px;
  margin-bottom: 20px;
}
.footer-wrap {
  display: flex;
  justify-content: space-between;
  position: relative;
}
.info-links-wrap {
  display: flex;
  column-gap: 210px;
}
.info-links-list ul {
  font-weight: 500;
}
.info-links-list li:nth-of-type(n + 2) {
  margin-top: 5px;
}
.copyright {
  position: absolute;
  left: 0;
  bottom: 0;
  font-weight: 500;
  opacity: 0.5;
}
.copyright a {
  padding: 0;
  margin: 0 5px;
}
/* ============ FOOTER END ============ */

/* ============ ADAPTIVE START ============ */
/* XX-Large devices (larger desktops) */
@media (max-width: 1599.98px) {
  .wide-container {
    max-width: 100%;
  }
  .container {
    max-width: 1350px;
  }
}

/* X-Large devices (large desktops, less than 1400px) */
@media (max-width: 1399.98px) {
  .container {
    max-width: 1170px;
  }
}
/* Large devices (desktops, less than 1200px) */
@media (max-width: 1199.98px) {
  .container {
    max-width: 990px;
  }
}

/* Medium devices (tablets, less than 992px) */
@media (max-width: 991.98px) {
  .container {
    max-width: 750px;
  }
}

/* Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
  .container {
    max-width: 570px;
  }
}

/* X-Small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .container {
    max-width: 100%;
  }
}
/* ============ ADAPTIVE END ============ */
